
.portfolio-item:after {
  content: "";
  display: table;
  clear: both;
}
@media (min-width: 769px) {
.portfolio-item .column {
    float: left;
}
.portfolio-item .column:first-of-type {
    width: 35%;
    margin-right: 5%;
}
.portfolio-item .column:last-of-type {
    width: 60%;
}
}
@media (min-width: 769px) and (max-width: 1024px) {
.portfolio-item .column:first-of-type {
    width: 45%;
}
}
@media (min-width: 769px) and (max-width: 1024px) {
.portfolio-item .column:last-of-type {
    width: 50%;
}
}
.portfolio-item h3 {
  font-size: 50px;
  font-size: 3.125rem;
}
.portfolio-item .content {
  margin-top: 60px;
  margin-top: 3.75rem;
}
.portfolio-item p {
  font-size: 26px;
  font-size: 1.625rem;
  font-style: italic;
}
.portfolio-item .logo img {
  max-width: 200px;
  max-height: 110px;
  width: auto;
}
@media (max-width: 768px) {
.portfolio-item .logo img {
    max-width: 150px;
    max-height: 80px;
}
}
.portfolio-item .vv-image {
  width: 100%;
}
.portfolio-item .portfolio .vv-image {
  border-radius: 5px;
  border-radius: 0.3125rem;
  overflow: hidden;
}
.portfolio-item .portfolio:not(:last-of-type) {
  margin-bottom: 20px;
  margin-bottom: 1.25rem;
}


