
.social-links {
  margin-top: 4rem;
}
.social-links a {
  color: #fff;
  text-decoration: none;
  font-size: 2rem;
  display: inline-block;
  margin: 0 1rem;
  opacity: 0.8;
  -webkit-transition: opacity 0.2s ease, text-shadow 0.4s ease, -webkit-transform 0.2s ease;
  transition: opacity 0.2s ease, text-shadow 0.4s ease, -webkit-transform 0.2s ease;
  transition: transform 0.2s ease, opacity 0.2s ease, text-shadow 0.4s ease;
  transition: transform 0.2s ease, opacity 0.2s ease, text-shadow 0.4s ease, -webkit-transform 0.2s ease;
}
.social-links a:hover,
.social-links a:active {
  opacity: 1;
  -webkit-transform: scale(1.25) rotate(-6deg);
          transform: scale(1.25) rotate(-6deg);
  text-shadow: 0 0 1rem rgba(0,0,0,0.1);
}
.social-links a:first-of-type {
  margin-left: 0;
}
.social-links a:last-of-type {
  margin-right: 0;
}


