
.link-banner {
  background: #fff;
  font-family: 'Arial', sans-serif;
  display: inline-block;
  padding: 10px 10px;
  padding: 0.625rem 20px;
  padding: 0.625rem 1.25rem;
  position: relative;
  margin-top: 50px;
  margin-top: 3.125rem;
  -webkit-box-shadow: 0 10px 30px rgba(0,0,0,0.1);
          box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  cursor: pointer;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.link-banner:hover {
  -webkit-transform: translateY(-5px) scale(1.03) rotate(-1deg);
          transform: translateY(-5px) scale(1.03) rotate(-1deg);
  -webkit-box-shadow: 0 30px 40px rgba(0,0,0,0.05);
          box-shadow: 0 30px 40px rgba(0,0,0,0.05);
}
.link-banner * {
  color: #c0b3e9;
  letter-spacing: 1px;
  letter-spacing: 0.0625rem;
  text-transform: uppercase;
  font-size: 14px;
  font-size: 0.875rem;
}
@media (max-width: 768px) {
.link-banner * {
    font-size: 12px;
    font-size: 0.75rem;
}
}
@media (max-width: 500px) {
.link-banner * {
    letter-spacing: 0;
}
}
.link-banner a {
  font-weight: bold;
  margin-left: 5px;
}
.link-banner .arrows {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
}
.link-banner .arrows:before,
.link-banner .arrows:after {
  content: ' ';
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
}
.link-banner .arrows-left {
  left: -19px;
  left: -1.1875rem;
}
.link-banner .arrows-left:before {
  top: 0;
  border-width: 0 20px 20px 0;
  border-width: 0 1.25rem 20px 0;
  border-width: 0 1.25rem 1.25rem 0;
  border-color: transparent #fff transparent transparent;
}
.link-banner .arrows-left:after {
  bottom: 0;
  border-width: 0 0 20px 20px;
  border-width: 0 0 1.25rem 20px;
  border-width: 0 0 1.25rem 1.25rem;
  border-color: transparent transparent #fff transparent;
}
.link-banner .arrows-right {
  right: 1px;
}
.link-banner .arrows-right:before {
  bottom: 0;
  border-width: 0 20px 20px 0;
  border-width: 0 1.25rem 20px 0;
  border-width: 0 1.25rem 1.25rem 0;
  border-color: transparent transparent #fff transparent;
}
.link-banner .arrows-right:after {
  top: 0;
  border-width: 0 0 20px 20px;
  border-width: 0 0 1.25rem 20px;
  border-width: 0 0 1.25rem 1.25rem;
  border-color: transparent transparent transparent #fff;
}


