
@media (min-width: 769px) {
.category-intro {
    padding-left: 60px;
    padding-left: 3.75rem;
}
}
.category-intro a.anchor {
  display: block;
  position: relative;
  top: -60px;
  top: -3.75rem;
  visibility: hidden;
}
.category-intro h2 {
  font-size: 100px;
  font-size: 6.25rem;
  position: relative;
  display: inline-block;
}
@media (max-width: 1200px) {
.category-intro h2 {
    font-size: 90px;
    font-size: 5.625rem;
}
}
@media (max-width: 768px) {
.category-intro h2 {
    font-size: 80px;
    font-size: 5rem;
}
}
@media (max-width: 500px) {
.category-intro h2 {
    font-size: 60px;
    font-size: 3.75rem;
}
}
@media (max-width: 350px) {
.category-intro h2 {
    font-size: 50px;
    font-size: 3.125rem;
}
}
.category-intro h2:after {
  content: ' ';
  background: #ddd6f3;
  height: 3px;
  position: absolute;
  bottom: 0;
  left: -2000px;
  right: 4px;
  right: 0.25rem;
}
.category-intro p {
  font-style: italic;
  max-width: 80%;
  margin-bottom: 6rem;
}
@media (max-width: 768px) {
.category-intro p {
    max-width: 95%;
    margin-bottom: 4rem;
}
}
.category-intro a.hoverable-light {
  font-weight: normal;
}


