
.intro {
  padding: 8rem 0;
  text-align: center;
}
@media (max-width: 768px) {
.intro {
    padding: 3rem 0 8rem;
}
}
.intro h2 {
  color: #fff;
  font-size: 3.5rem;
  font-style: italic;
  max-width: 850px;
  max-width: 53.125rem;
  margin: 0 auto;
  font-weight: normal;
}
@media (max-width: 1200px) {
.intro h2 {
    max-width: 700px;
    max-width: 43.75rem;
    font-size: 2.8rem;
}
}
@media (max-width: 768px) {
.intro h2 {
    font-size: 2.2rem;
    max-width: 550px;
    max-width: 34.375rem;
}
}
@media (max-width: 500px) {
.intro h2 {
    font-size: 2rem;
    max-width: 370px;
    max-width: 23.125rem;
}
}


