
.logo-garden {
  padding-left: 0;
  padding-right: 0;
}
@media (max-width: 500px) {
.logo-garden {
    text-align: center;
}
}
.logo-garden img {
  max-width: 200px;
  max-width: 12.5rem;
  max-height: 40px;
  max-height: 2.5rem;
}
@media (max-width: 500px) {
.logo-garden img {
    width: 100%;
    max-height: initial;
}
}
.logo-garden .vv-visual {
  display: inline-block;
  vertical-align: middle;
  margin-right: 80px;
  margin-right: 5rem;
  margin-bottom: 50px;
  margin-bottom: 3.125rem;
}
@media (max-width: 1368px) {
.logo-garden .vv-visual {
    margin-right: 50px;
    margin-right: 3.125rem;
    margin-bottom: 30px;
    margin-bottom: 1.875rem;
}
}
@media (max-width: 500px) {
.logo-garden .vv-visual {
    margin: 0 0 30px 0;
    margin: 0 0 1.875rem 0;
    width: 40%;
}
.logo-garden .vv-visual:nth-of-type(odd) {
    margin-right: 4%;
}
.logo-garden .vv-visual:nth-of-type(even) {
    margin-left: 4%;
}
}


