
.site-footer {
  position: relative;
  margin-top: 6rem;
}
.site-footer .category-intro {
  padding-top: 0;
  padding-bottom: 70px;
  padding-bottom: 4.375rem;
  z-index: 1;
  position: relative;
}
.site-footer .category-intro .heading:after {
  background-color: rgba(250,172,168,0.5);
}
.site-footer:before {
  content: ' ';
  background: #fff;
  position: absolute;
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
  width: 120%;
  height: 120%;
  top: -10%;
  left: -10%;
  z-index: 0;
  -webkit-box-shadow: 0 0 50px rgba(0,0,0,0.05);
          box-shadow: 0 0 50px rgba(0,0,0,0.05);
}
.site-footer p {
  color: #b7a8e6;
}
.site-footer h2 {
  color: #cec4ee;
}
.site-footer .copyright {
  margin-top: 60px;
  margin-top: 3.75rem;
  color: rgba(250,172,168,0.8);
  text-transform: uppercase;
  letter-spacing: 4px;
  letter-spacing: 0.25rem;
  font-weight: bold;
  font-size: 12px;
  font-size: 0.75rem;
  font-family: 'Arial', sans-serif;
}
.site-footer .social-links a {
  color: rgba(250,172,168,0.8);
}


